import React, { useState } from 'react';
import styles from './Describe.module.css';
import { Button } from './basic/Button';
import { styleAs } from '../utility/style';
import { Column } from '../layout/Column';
import { useStateExtPersisted } from '../utility/store';
import { SetPair } from '../hooks/useStateExt';

interface IconConfig {
  collapsed: string;
  expanded: string;
}

export const ICONS_ARROWS: IconConfig = {
  collapsed: '>',
  expanded: '<',
};

export const ICONS_QUESTION: IconConfig = {
  collapsed: '?',
  expanded: 'x',
};

export interface DescribeProps {
  preventCollapse?: boolean;
  onToggle?: (value: boolean) => void;

  collapsed?: boolean;
  collapsedPair?: SetPair<boolean>;

  icons?: IconConfig;
}

export const Describe: React.FC<DescribeProps> = (props) => {
  const {
    children,
    collapsed: defCollapsed,
    onToggle,
    preventCollapse,
    icons = ICONS_ARROWS,
    collapsedPair,
  } = props;

  const [collapsed, setCollapsed] = collapsedPair ?? useState(defCollapsed);

  const extras = [];
  if (collapsed) {
    extras.push(styles.collapsed);
  } else {
    extras.push(styles.expanded);
  }
  return (
    <>
      <div className={styleAs(styles.describeWrap, extras)}>
        {!preventCollapse && <Button
          className={styles.btn}
          action={() => {
            const result = !collapsed;
            onToggle?.(result);
            setCollapsed(result);
          }}
        >
          {
            collapsed
              ? icons.collapsed
              : icons.expanded
          }
        </Button>
        }
        {!collapsed &&
          <div className={styles.describe}>
            <Column>
              {children}
            </Column>
          </div>
        }
      </div>
    </>
  );
};

export const PersistDescribe: React.FC<Omit<DescribeProps, 'collapsed'>> = (props) => {
  const { preventCollapse } = props;

  const [, , collapsedPair] = useStateExtPersisted('hideHelp', !preventCollapse);

  return (
    <Describe collapsedPair={collapsedPair} {...props} />
  );
};