import React, { useState, useMemo } from 'react';
import { useId } from '../../hooks/useId';
import styles from './basic.module.css';
import { SetPair } from '../../hooks/useStateExt';
import { styleAs } from '../../utility/style';

export interface InputProps {
  defaultValue?: string;
  onChange?: (v: string) => void;

  setPair?: SetPair<string>;

  label?: React.ReactNode;

  readOnly?: boolean;
  center?: boolean;
}

export const Input: React.FC<InputProps> = (props) => {
  const { defaultValue, onChange, label, setPair, readOnly, center } = props;

  const [value, setValue] = setPair ?? useState(defaultValue);
  const id = useId();

  const extraProps = useMemo(() => {
    const result: any = {};

    if (readOnly) {
      result.disabled = 'disabled';
    }

    return result;
  }, [readOnly]);

  const extras = [];
  if (center) {
    extras.push(styles.center);
  }

  return (
    <div className={styles.inputWrap}>
      {label && <label htmlFor={id}>{label}</label>}
      <input
        {...extraProps}

        className={styleAs(styles.input, extras)}
        id={id}
        value={value}
        onChange={({ target: { value: v } }) => {
          setValue(v);
          onChange?.(v);
        }}
      />
    </div>
  );
};